.root {
    width: 100%;
    display: flex;
    justify-content: center;
}

.elevate {
    width: 30%;
    border-radius: 20px;
    box-shadow: 10px 10px 40px;
    margin: 5vh;
    background-color: rgb(255, 255, 255);
    padding: 10vh;
    padding-top: 0vh;
    padding-bottom: 10vh;
}

.form {
    display: flex;
    justify-content: center;
}

.reset {
    display: flex;
    justify-content: center;
    padding-top: 3vh;
}

@media only screen and (max-width: 800px) {
    .elevate {
        width: 100%;
        background-color: inherit;
        box-shadow: none;
        padding: 0;
    }
}